import { getCookie, setCookie } from "typescript-cookie"
import { MARKETING_COOKIE_NAME, getMarketingCookie, setMarketingCookie } from "../marketingCookie"

const mockGetCookie = getCookie as jest.Mock
const mockSetCookie = setCookie as jest.Mock

jest.mock("typescript-cookie", () => ({
  getCookie: jest.fn(),
  setCookie: jest.fn(),
}))

describe("setMarketingCookie", () => {
  beforeEach(() => {
    jest.clearAllMocks()
  })

  it("should get back a default object", () => {
    expect(getMarketingCookie()).toEqual({})
  })

  it("should call the setCookie", () => {
    setMarketingCookie(true, "123")
    expect(mockGetCookie).toHaveBeenCalledWith(MARKETING_COOKIE_NAME)
    expect(mockSetCookie).toHaveBeenCalledWith(MARKETING_COOKIE_NAME, expect.any(String), {
      domain: ".bigdata.com",
      path: "/",
      secure: true,
    })
  })
})
